import { Component, OnInit, Injector, HostListener, Renderer2, ElementRef, ViewChild } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { UserService } from 'src/app/api/controllers/User';
import { ScreenService } from 'src/app/api/controllers/Screen';
import { SharedService } from '../../../shared/services/shared.service';
import { ModalService } from '../../../shared/services/modal.service';
import { ConstantsService } from '../../../shared/services/constants.service';
import { CookieService } from 'ngx-cookie-service';

import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';

import * as _ from 'underscore';
import * as moment from 'moment';

declare var $:JQueryStatic;

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {

  activeNav:string = "";

  isSideBarMini:boolean = false;

  isApartmentSelected:boolean = false;

  apartmentName:string = "";

  modalService:any;

  user:any;
  userLoggedIn:boolean = false;

  isMenuLoaded:boolean = false;

  availableScreensDataList:any;
  menusDataList:any;

  sidebarMenuDataList = [];

  activePath:any;

  public config: PerfectScrollbarConfigInterface = {};

  @ViewChild("navElement", { static: false }) navElementRef: ElementRef<any>;

  constructor(
  private renderer: Renderer2,
  private injector: Injector,
  private router: Router,
  private apartmentService:ApartmentService,
  private userService:UserService,
  private screenService:ScreenService,
  private cookieService:CookieService,
  private constantsService:ConstantsService,
  private sharedService:SharedService
  ) { 

    this.modalService = this.injector.get(ModalService);

  	router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
          if(window.innerWidth <= 991){
            this.isSideBarMini = false;
            this.sharedService.setSideBarMini(this.isSideBarMini);
          }
          this.activePath = this.router.url;
      }
    });

  }

  toggleNav(name){

    var elem = this.navElementRef.nativeElement;
    this.renderer.removeClass(elem,"oh");

    if(this.activeNav != name){
      this.activeNav = name
    }
    else {
      this.activeNav = ""
    }

  }

  isNavActive(name){
  	return ( this.activeNav == name ) ? 'nav-active' : '';
  }

  showMiniSideBar(){
    return this.isSideBarMini ? 'mini' : '';
  }

  showApartments(){
    this.modalService.showApartmentModal();
  }

  showUserInfo(){
    this.modalService.showUserInfoModal(this.user);
  }

  gotoExtPage(){
    this.router.navigate(['http://108.179.237.164/~actualso/projects/99apartment/']);
  }

  stopPropagation(event: Event){
    event.stopPropagation();
  }

  isSubMenusAvailable(submenu){
    return (submenu == undefined || submenu.length == 0) ? false : true
  }

  @HostListener('window:scroll', ['$event']) 
  onScroll(event) {
    const hasClass = (el, className) => el.classList.contains(className);
    if(hasClass(document.querySelector('.sidebar'), 'mini')){
      var elem = this.navElementRef.nativeElement;
      this.renderer.addClass(elem,"oh");
      this.activeNav = "";
    }
  }

  ngOnInit() {

    this.isMenuLoaded = false;

    this.menusDataList = this.constantsService.getAllMenus();

    let params = {
      active: 1
    }

    this.screenService.getAllScreensByStatus(params).subscribe((res:any) => {

        this.availableScreensDataList = res;

        this.isMenuLoaded = true;

        _.each(this.availableScreensDataList, item => {

          var menus = this.menusDataList.filter(menu => {
            return menu.name == item.screenName;
          });

          //undefined check if local json doesnt have corrosponding menu data
          if(menus[0] != undefined)
          this.sidebarMenuDataList.push(menus[0]);
          
        });
        

        setTimeout(() => {

          var navItems = $(".nav-list");

          $.each(navItems, (index, elem) => {

            if($(elem).find('.nav-link').hasClass('active')){
              this.activeNav = $(elem).find('.nav-link').attr('name');
            }

          });


        }, 1000);

    },
    error => {
      this.isMenuLoaded = true;
    });


    var userId = parseInt(this.cookieService.get('userId'));

    let userParams = {
      userid: userId
    }

    this.userService.getUserById(userParams).subscribe((res:any) => {
      
      this.user = res[0];
      this.user.role = this.cookieService.get('userRole');
      this.sharedService.setUserDetails(this.user);
      this.sharedService.isUserLogged(true);

     

    },
    error => {
      console.log(error);
    });

    this.sharedService.apartmentselectedcast.subscribe( isSelected => {

      this.isApartmentSelected = isSelected;

      if(this.isApartmentSelected){

        var params = {
          apartmentId : parseInt(this.cookieService.get('apartmentId')),
          active:1
        }

        this.apartmentService.getApartmentByApartmentId(params).subscribe((res:any) => {
          
          this.apartmentName = res[0].apartmentName;
          

        },
        error => {
          console.log(error);
        });

      }

    });

    this.sharedService.userdetailscast.subscribe( user => {
      this.user = user;
    });

    this.sharedService.sidebarminicast.subscribe(isSideBarMini => this.isSideBarMini = isSideBarMini);
    this.sharedService.userlogin.subscribe( userLoggedIn => this.userLoggedIn = userLoggedIn );

  }

}

