import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AmsComponent } from './ams.component';
import { SettingsComponent } from './components/settings/settings.component';
import { PaymentSetupComponent } from './components/settings/payment-setup/payment-setup.component';

import { AuthGuard } from '.././auth.guard';
import { OperationalMaintananceModule } from './operational-maintanance/operational-maintanance.module';


export const routes: Routes = [
	
	{ path: '', component: AmsComponent,
	
		children: [
			{ path: '', redirectTo: 'dashboard', pathMatch: 'full'},
			{ path: 'dashboard', loadChildren: () => import('./dashboard/dashboard.module').then(m => m.DashboardModule) },
			{ path: 'my-property', loadChildren: () => import('./my-property/my-property.module').then(m => m.MyPropertyModule) },
			{ path: 'unit&users', loadChildren: () => import('./unit-users/unit-users.module').then(m => m.UnitUsersModule) },
			{ path: 'inbox', loadChildren: () => import('./ams-inbox/ams-inbox.module').then(m => m.AmsInboxModule) },
			{ path: 'helpdesk', loadChildren: () => import('./helpdesk/helpdesk.module').then(m => m.HelpdeskModule) },
			{ path: 'income', loadChildren: () => import('./income-tracker/income-tracker.module').then(m => m.IncomeTrackerModule) },
			{ path: 'expense', loadChildren: () => import('./expense-tracker/expense-tracker.module').then(m => m.ExpenseTrackerModule) },
			{ path: 'general-ledger', loadChildren: () => import('./general-ledger/general-ledger.module').then(m => m.GeneralLedgerModule) },
			{ path: 'bank', loadChildren: () => import('./bank-cash/bank-cash.module').then(m => m.BankCashModule) },
			{ path: 'work-permit', loadChildren: () => import('./work-permit/work-permit.module').then(m => m.WorkPermitModule) },
			{ path: 'utility', loadChildren: () => import('./utility/utility.module').then(m => m.UtilityModule) },
			{ path: 'broadcast', loadChildren: () => import('./broadcast/broadcast.module').then(m => m.BroadcastModule) },
			{ path: 'security', loadChildren: () => import('./security/security.module').then(m => m.SecurityModule) },
			{ path: 'visitor', loadChildren: () => import('./visitor/visitor.module').then(m => m.VisitorModule) },
			{ path: 'staff', loadChildren: () => import('./staff-manager/staff-manager.module').then(m => m.StaffManagerModule) },
			{ path: 'moveinout-tracker', loadChildren: () => import('./moveinout-tracker/moveinout-tracker.module').then(m => m.MoveinoutTrackerModule) },
			{ path: 'profile-settings', loadChildren: () => import('./profile-settings/profile-settings.module').then(m => m.ProfileSettingsModule) },
			{ path: 'my-profile', loadChildren: () => import('./profile-area/profile-area.module').then(m => m.ProfileAreaModule) },
			{ path: 'parking-management', loadChildren: () => import('./parking-management/parking-management.module').then(m => m.ParkingManagementModule) },
			{ path: 'facility', loadChildren: () => import('./facility/facility.module').then(m => m.FacilityModule) },
			{ path: 'meetings', loadChildren: () => import('./meetings/meetings.module').then(m => m.MeetingsModule) },
			{ path: 'assets', loadChildren: () => import('./assets/assets.module').then(m => m.AssetsModule) },
			{ path: 'inventory', loadChildren: () => import('./inventory/inventory.module').then(m => m.InventoryModule) },
			{ path: 'notice', loadChildren: () => import('./notice/notice.module').then(m => m.NoticeModule) },
			{ path: 'vendor', loadChildren: () => import('./vendor/vendor.module').then(m => m.VendorModule) },
			{ path: 'documents', loadChildren: () => import('./documents/documents.module').then(m => m.DocumentsModule) },
			{ path: 'directory', loadChildren: () => import('./directory/directory.module').then(m => m.DirectoryModule) },
			{ path: 'operational-maintanance', loadChildren: () => import('./operational-maintanance/operational-maintanance.module').then(m => m.OperationalMaintananceModule) },
			{ path: '**', redirectTo: 'dashboard', pathMatch: 'full'}
		]

	}
		
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AmsRoutingModule { 

}
