import { Component, OnInit } from '@angular/core';

import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

import { SharedService } from '../../../shared/services/shared.service';
import { ConstantsService } from '../../../shared/services/constants.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-ams-bottom-menu',
  templateUrl: './ams-bottom-menu.component.html',
  styleUrls: ['./ams-bottom-menu.component.scss']
})
export class AmsBottomMenuComponent implements OnInit {
  
  isSideBarMini:boolean = false;

  constructor(
  	private userService:UserService,
    private apartmentService:ApartmentService,
    private constantsService:ConstantsService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  toggleSidebar() {
  	this.isSideBarMini = !this.isSideBarMini;
  	this.sharedService.setSideBarMini(this.isSideBarMini);
  }

  isSideBarActive(){
  	return this.isSideBarMini == true ? 'active' : '';
  }

  ngOnInit() {

  	this.sharedService.sidebarminicast.subscribe(isSideBarMini => this.isSideBarMini = isSideBarMini);

  }

}
